import React, { Component } from 'react';
import { Form, Input, Button, message } from 'antd';
import { UserOutlined, LockOutlined, UnlockOutlined, PhoneOutlined } from '@ant-design/icons';
import { reqAddUser } from '../../api';

import './style.less';

class CreateAccount extends Component {
  onFinish = async (values) => {
    const { username, password, surePassword, phone } = values;
    if (password !== surePassword) {
      message.error('两次输入的密码不匹配');
      return;
    }
    const result = await reqAddUser({
      name: username,
      password,
      confirmPassword: surePassword,
      phone,
      userType: '0',
    });
    if (result.status === 200) {
      message.success('创建账号成功');
    } else {
      message.error(result.message);
    }
  };

  render() {
    return (
      <div className="register">
        <header className="login-header">创建账号</header>
        <section className="login-content">
          <Form name="normal_login" className="login-form" onFinish={this.onFinish}>
            <Form.Item
              name="username"
              rules={[{ required: true, message: 'Please input your Username!' }]}
            >
              <Input
                prefix={<UserOutlined className="site-form-item-icon" />}
                placeholder="Username"
              />
            </Form.Item>
            <Form.Item
              name="password"
              rules={[
                { required: true, message: 'Please input your Password!' },
                { min: 4, message: '最少4位' },
                { max: 16, message: '最多16位' },
                { pattern: /^[a-zA-Z0-9_]+$/, message: '密码格式不对' },
              ]}
            >
              <Input
                prefix={<UnlockOutlined className="site-form-item-icon" />}
                type="password"
                placeholder="Password"
              />
            </Form.Item>

            <Form.Item
              name="surePassword"
              rules={[
                { required: true, message: 'Please input your Password!' },
                { min: 4, message: '最少4位' },
                { max: 16, message: '最多16位' },
                { pattern: /^[a-zA-Z0-9_]+$/, message: '密码格式不对' },
              ]}
            >
              <Input
                prefix={<LockOutlined className="site-form-item-icon" />}
                type="password"
                placeholder="Password"
              />
            </Form.Item>

            <Form.Item
              name="phone"
              rules={[
                { required: true, message: 'Please input your Password!' },
                { pattern: /^1[3456789]\d{9}$/, message: '请输入正确的手机号' },
              ]}
            >
              <Input
                prefix={<PhoneOutlined className="site-form-item-icon" />}
                placeholder="phone"
              />
            </Form.Item>

            <Form.Item>
              <Button type="primary" htmlType="submit" className="login-form-button">
                立即创建
              </Button>
            </Form.Item>
          </Form>
        </section>
      </div>
    );
  }
}

export default CreateAccount;
