import React, { Component } from 'react';
import Paper from '../../components/Paper';

class MyPaper extends Component {
  render() {
    const { papers } = this.props;
    return (
      <div className="showPaper-content">
        <h4>我的试题</h4>
        <Paper result={papers}></Paper>
      </div>
    );
  }
}

export default MyPaper;
