import React, { Component } from 'react';
import { Button } from 'antd';
import { showPaperType } from './Variable';
import Main from '../../components/Paper';
import TeacherTree from '../../components/TeacherTree';
import { reqGetAllQuestions, reqGetQuestionsLeftMenu, reqGetQuestions } from '../../api';
import memoryUtils from "../../utils/memoryUtils";

class ShowPaper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectPaper: {},
      treeList: [],
      topData: {
        complexity: ['容易', '较易', '一般', '难', '较难', '困难'],
        questionsType: ['单选题', '多选题', '判断题', '填空题', '解答题'],
      },
      ShowPaperResult: [],
    };
    this.payload = {
      complexity: null,
      course: null,
      grade: null,
      knowledge: null,
      label: null,
      questionsType: null,
      year: null,
    };
  }
  async componentDidMount() {
    const result = await reqGetQuestionsLeftMenu();
    const showPaperResult = await reqGetQuestions(this.payload);
    const paperResult = await reqGetAllQuestions();
    if (result.data) {
      this.setState({ treeList: result.data });
    }
    if (paperResult.data) {
      this.setState({
        topData: {
          ...paperResult.data,
          questionsType: ['单选题', '多选题', '判断题', '填空题', '解答题'],
          complexity: ['容易', '较易', '一般', '难', '较难', '困难'],
        },
      });
    }
    if (showPaperResult.data) {
      this.setState({ ShowPaperResult: showPaperResult.data });
    }
  }

  handleClick = (key, d) => {
    const { selectPaper } = this.state;
    const newSelect = selectPaper[key] ? [...selectPaper[key]] : [d];
    if (selectPaper[key]) {
      const num = selectPaper[key] ? newSelect.indexOf(d) : -1;
      if (num > -1) {
        newSelect.splice(num, 1);
      } else {
        newSelect.push(d);
      }
    }
    this.setState({ selectPaper: { ...selectPaper, [key]: newSelect } }, () => {
      this.load();
    });
  };
  handleChange = (value) => {
    if (value.length > 0) {
      const valueArr = value[0].split('|');
      const obj ={
        grade: valueArr[1] || null,
        course: valueArr[2] || null,
        knowledge: valueArr[3] || null,
      };
      const payload ={
        ...this.payload,
        ...obj
      }
      setTimeout(() => {
        this.load(payload);
      });
    }else {
      this.load();
    }
    // 改变查看状态，发送请求
  };

  async load(pay) {
    const { selectPaper } = this.state;
    const user = memoryUtils.user;
    const payload = {
      ...this.payload,
      ...pay,
      ...selectPaper,
      id:user.id
    };
    const showPaperResult = await reqGetQuestions(payload);
    if (showPaperResult.data) {
      this.setState({ ShowPaperResult: showPaperResult.data });
    }
  }
  render() {
    const { selectPaper, treeList, topData, ShowPaperResult } = this.state;
    return (
      <div className="showPaper">
        <div className="showPaper-left">
          <Button>素材选取</Button>
          <TeacherTree treeList={[...treeList]} onChange={this.handleChange} />
        </div>
        <div className="showPaper-right">
          <div className="showPaper-tab">
            {Object.keys(showPaperType).map((t, index) => {
              return (
                <div className="tab-main" key={index}>
                  <span key={t} className="tab-main-text">
                    {t}
                  </span>
                  <div style={{ marginTop: 10 }}>
                    {((topData[showPaperType[t]] && topData[showPaperType[t]]) || []).map(
                      (d, shortIndex) => {
                        let newValue = d;
                        if (t === '难度' || t === '题型') {
                          newValue = shortIndex + 1;
                        }
                        return (
                          <span
                            key={`${t}-${d}`}
                            className={`tab-main-span ${
                              (selectPaper[showPaperType[t]] || []).indexOf(newValue) > -1
                                ? 'selected'
                                : ''
                            }`}
                            onClick={() => {
                              this.handleClick(showPaperType[t], newValue);
                            }}
                          >
                            {d}
                          </span>
                        );
                      }
                    )}
                  </div>
                </div>
              );
            })}
          </div>
          <div className="showPaper-content">
            <Main result={ShowPaperResult}  showId='showPaper'/>
          </div>
        </div>
      </div>
    );
  }
}

export default ShowPaper;
