import React, {Component} from 'react';
import {Input, Button, message} from 'antd';
import memoryUtils from '../../utils/memoryUtils';
import {
    reqAddTeach,
    reqDeleteTeach,
    reqAllTeach,
    reqAddTeachToMe,
    reqUpdateTeach, reqGetQuestionsAnswer,
} from '../../api';
import AddPlanModal from '../addPlanModal';
import * as CONST from './const';
import './style.less';
import Main from '../Paper';

const {TextArea} = Input;
const selectArr = ['一', '二', '三', '四', '五', '六', '七', '八', '九', '十'];
const USER = memoryUtils.user;

class Plan extends React.PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            planParams: {
                ...CONST.PLAN_PARAMS,
                ...props.planParams,
            },
            isShowCompleted: props.isShowCompleted || false,
            isEditable: props.isEditable || false,
            planType: props.planType || 'edit',
        };
    }

    handleChange = (key, value, index, parentId, week) => {
        const {planParams} = this.state;
        const {teachPointsList, teachPracticeList} = planParams;
        if (parentId === 'teachPointsList') {
            const obj = {...teachPointsList[index]};
            if (key === 'pointsQuestionIds') {
                obj[key] = Object.keys(value);
                obj[`${key}_detail`] = value;
            } else {
                obj[key] = value;
            }
            const newArr = [...teachPointsList];
            newArr[index] = obj;
            this.setState({
                planParams: {
                    ...planParams,
                    teachPointsList: newArr,
                },
            });
        } else if (key === 'seniorQuestionIds') {
            this.setState({
                planParams: {
                    ...planParams,
                    seniorQuestionIds: Object.keys(value).join(','),
                    seniorQuestionIds_detail: value
                }
            });
        } else if (key === 'consolidateQuestionIds') {
            this.setState({
                planParams: {
                    ...planParams,
                    consolidateQuestionIds: Object.keys(value).join(','),
                    consolidateQuestionIds_detail: value,
                }
            });
        } else if (parentId === 'teachPracticeList') {
            let obj = {};
            if (key === 'practiceQuestionIds') {
                obj = {...teachPracticeList[index], week: index};
                obj[key] = Object.keys(value).join(',');
                obj[`${key}_detail`] = value;
            } else {
                if (key === 'signature') {
                    obj = {...teachPracticeList[index], week}
                }
                obj[key] = value;
            }
            const newArr = [...teachPracticeList];
            const num = key === 'practiceQuestionIds' ? newArr.findIndex((s) => s.week === index) : index;
            if (num > -1) {
                if (newArr[num]) {
                    newArr[num] = obj
                } else {
                    newArr.push(obj);
                }
            } else {
                newArr.push(obj);
            }
            this.setState({
                planParams: {
                    ...planParams,
                    teachPracticeList: newArr,
                },
            });
        } else {
            this.setState({
                planParams: {
                    ...planParams,
                    [key]: value,
                },
            });
        }
    };

    handlePlan = async (key, result) => {
        if (key === 'showAllEdit') {
            // 个人中心的查看并修改
            const payload = {
                id: result.id,
            };
            const resultAllEdit = await reqAllTeach(payload);
            if (resultAllEdit) {
                if (resultAllEdit.data && resultAllEdit.data.id) {
                    const newId = String(resultAllEdit.data.id) || '';
                    const lastId =  String(this.state.planParams.id) || '';
                    if(newId === lastId){
                        let newParams = {...resultAllEdit.data};
                        const Arr= [];
                        (resultAllEdit.data.teachPointsList ||[]).forEach((s,index)=>{
                            Arr.push(index+1)
                        })
                        newParams.importArr = Arr
                        this.setState({planParams:newParams});
                    }
                
                }
            } 
                this.setState({isEditable: true, isShowCompleted: true});

        } else if (key === 'showAll') {
            this.setState({isShowCompleted: true});
            const payload = {
                id: result.id,
            };
            const resultAllItem = await reqAllTeach(payload);
            if (resultAllItem) {
                if (resultAllItem.data && resultAllItem.data.id) {
                    const newId = String(resultAllItem.data.id) || '';
                    const lastId =  String(this.state.planParams.id) || '';
                    if(newId === lastId){
                        let newParams = {...resultAllItem.data};
                        const importArr= [];
                        (newParams.teachPointsList ||[]).forEach((s,index)=>{
                            importArr.push(index+1)
                        })
                        newParams.importArr = importArr
                        this.setState({planParams:newParams});
                    }

                }
            }
        } else if (key === 'disShowAll') {
            this.setState({isShowCompleted: false});
        } else if (key === 'delete') {
            const payload = {
                teachId: result.id || 1,
            };
            const result = await reqDeleteTeach(payload);
        }
        if (key === 'addSelf') {
            const payload = {
                teachId: result.id || 1,
                userId: USER.id || 1,
                questionsId: result.id,
            };
            const newResult = await reqAddTeachToMe(payload);
        }
    };

    handleClick = () => {
        const {planParams} = this.state;
        const {importArr} = planParams;
        const num = importArr[importArr.length - 1];
        if (num >= 10) {
            message.warn('添加考点过多');
        } else {
            this.setState({
                planParams: {
                    ...planParams,
                    importArr: [...importArr, num + 1],
                },
            });
        }
    };

    handleCreatePlan = async () => {
        const {planParams} = this.state;
        const {planType} = this.props;
        const {teachPointsList, teachPracticeList} = planParams;
        const obj = {
            teachPointsList: teachPointsList.map((t) => {
                if (t.pointsQuestionIds) {
                    t.pointsQuestionIds = Array.isArray(t.pointsQuestionIds)
                        ? t.pointsQuestionIds.join(',')
                        : t.pointsQuestionIds;
                }
                return t;
            }),
            teachPracticeList: teachPracticeList.map((t) => {
                if (t.practiceQuestionIds) {
                    t.practiceQuestionIds = Array.isArray(t.practiceQuestionIds)
                        ? t.practiceQuestionIds.join(',')
                        : t.practiceQuestionIds;
                }
                return t;
            }),
        };
        const payload = {
            ...planParams,
            ...obj,
            teach_id: planParams.id || '',
            id: USER.id || 1,
        };

        let result = planType === 'add' ? await reqAddTeach(payload) : await reqUpdateTeach(payload);
        if (result.status === 200) {
            message.success(result.message);
        } else {
            message.error(result.message);
        }
    };
    render() {
        const {
            planParams,
            isShowCompleted,
            isEditable,
            planType,
        } = this.state;
        const {teachPointsList, teachPracticeList,seniorQuestionIds_detail,consolidateQuestionIds_detail} = planParams;
        return (
            <div className="plan">
                <div className="plan-describe">
                    <label className="plan-title">教案B</label>
                    <div className="plan-describe-inline-item">
                        {CONST.Teach_First_Line.map((item) => {
                            return (
                                <div className="plan-describe-inline-item-item" key={item.key}>
                                    <span>{item.label}</span>
                                    <Input
                                        value={planParams[item.key] || ''}
                                        disabled={!isEditable}
                                        onChange={(e) => this.handleChange(item.key, e.target.value)}
                                    />
                                </div>
                            );
                        })}
                    </div>
                    <div className="plan-describe-inline-item">
                        {CONST.Student_First_line.map((item) => {
                            return (
                                <div className="plan-describe-inline-item-item" key={item.key}>
                                    <span>{item.label}</span>
                                    <Input
                                        value={planParams[item.key] || ''}
                                        disabled={!isEditable}
                                        onChange={(e) => this.handleChange(item.key, e.target.value)}
                                    />
                                </div>
                            );
                        })}
                    </div>
                    <div className="plan-describe-secondline">
                        {CONST.Teach_Second_Line.map((item) => {
                            return (
                                <div className="plan-describe-secondline-item" key={item.key}>
                                    <span>{item.label}</span>
                                    <Input
                                        value={planParams[item.key] || ''}
                                        disabled={!isEditable}
                                        onChange={(e) => this.handleChange(item.key, e.target.value)}
                                    />
                                </div>
                            );
                        })}
                    </div>
                    {CONST.Teach_Describe.map((item) => {
                        return (
                            <div className="plan-describe-single" key={item.key}>
                                <span>{item.label}</span>
                                <TextArea
                                    value={planParams[item.key] || ''}
                                    className="TextArea"
                                    disabled={!isEditable}
                                    onChange={(e) => this.handleChange(item.key, e.target.value)}
                                    autoSize={{minRows: 2, maxRows: 5}}
                                />
                            </div>
                        );
                    })}
                </div>

                {isShowCompleted && (
                    <div className="plan-content">
                        <div className="plan-content-title">教学内容</div>
                        <div className="plan-content-top-wrapper">
                            <div className="plan-content-left">
                                {CONST.Teach_Content_Top2.map((item) => {
                                    return (
                                        <div className="content-input" key={item.key}>
                                            <span>{item.label}</span>
                                            <TextArea
                                                onChange={(e) => this.handleChange(item.key, e.target.value)}
                                                className="TextArea"
                                                value={planParams[item.key] || ''}
                                                disabled={!isEditable}
                                                autoSize={{minRows: 5, maxRows: 15}}
                                            />
                                        </div>
                                    );
                                })}
                                {(planParams.importArr || []).map((t, index) => {
                                    return (
                                        <div className="content-input" key={index}>
                                            <span>{`考点${selectArr[index]}`}</span>
                                            <div>
                                                <span>考点梳理:</span>
                                                <TextArea
                                                    onChange={(e) =>
                                                        this.handleChange('points', e.target.value, index, 'teachPointsList')
                                                    }
                                                    className="TextArea"
                                                    value={(planParams.teachPointsList[index] || {}).points || ''}
                                                    disabled={!isEditable}
                                                    style={{border: '1px solid #888'}}
                                                    autoSize={{minRows: 5, maxRows: 15}}
                                                />
                                            </div>
                                            <div>
                                                <span>剖析:</span>
                                                <TextArea
                                                    onChange={(e) =>
                                                        this.handleChange('analyse', e.target.value, index, 'teachPointsList')
                                                    }
                                                    className="TextArea"
                                                    value={(planParams.teachPointsList[index] || {}).analyse || ''}
                                                    disabled={!isEditable}
                                                    style={{border: '1px solid #888'}}
                                                    autoSize={{minRows: 5, maxRows: 15}}
                                                />
                                            </div>
                                            <div>
                                                <span>小试牛刀</span>
                                                {teachPointsList[index] &&
                                                teachPointsList[index]['pointsQuestionIds_detail'] && (
                                                    <div>
                                                        {Object.keys(teachPointsList[index]['pointsQuestionIds_detail']).map(
                                                            (td) => {
                                                                return (
                                                                    <Main
                                                                        result={[
                                                                            teachPointsList[index]['pointsQuestionIds_detail'][td],
                                                                        ]}
                                                                        showId="createPlanPaper"
                                                                    />
                                                                );
                                                            }
                                                        )}
                                                    </div>
                                                )}
                                                <AddPlanModal
                                                    disabled={!isEditable}
                                                    onChange={this.handleChange}
                                                    Main="pointsQuestionIds"
                                                    parentId="teachPointsList"
                                                    index={index}
                                                />
                                            </div>
                                        </div>
                                    );
                                })}
                                <div className="continue-add">
                                    <span>继续添加</span>
                                    <div
                                        className="content-btn"
                                        style={{marginLeft: '30%'}}
                                        onClick={this.handleClick}
                                    >
                                        +
                                    </div>
                                </div>
                                <div className="content-input">
                                    <span>3.【中考演练】</span>
                                    <TextArea
                                        onChange={(e) => this.handleChange('seniorQuestion', e.target.value)}
                                        className="TextArea"
                                        disabled={!isEditable}
                                        value={planParams['seniorQuestion'] || ''}
                                        autoSize={{minRows: 5, maxRows: 15}}
                                    />
                                    {seniorQuestionIds_detail && Object.keys(seniorQuestionIds_detail).length > 0 && (
                                        <div>
                                            {Object.keys(seniorQuestionIds_detail).map((ser) => {
                                                return (
                                                    <Main result={[seniorQuestionIds_detail[ser]]}
                                                          showId="createPlanPaper"/>
                                                );
                                            })}
                                        </div>
                                    )}
                                    <AddPlanModal
                                        disabled={!isEditable}
                                        onChange={this.handleChange}
                                        Main="seniorQuestionIds"
                                    />
                                </div>
                                <div className="content-input">
                                    <span>4.【总结归纳】</span>
                                    <TextArea
                                        onChange={(e) => this.handleChange('conclude', e.target.value)}
                                        className="TextArea"
                                        disabled={!isEditable}
                                        value={planParams['conclude'] || ''}
                                        autoSize={{minRows: 5, maxRows: 15}}
                                    />
                                </div>
                            </div>
                            <div className="plan-content-right">
                                <span className="title">笔记</span>
                                <TextArea
                                    onChange={(e) => this.handleChange('node', e.target.value)}
                                    className="TextArea"
                                    disabled={!isEditable}
                                    value={planParams['node'] || ''}
                                    style={{height: 'calc(100% - 31px)'}}
                                />
                            </div>
                        </div>

                        <div className="plan-content-title">自我巩固</div>
                        <div className="content-modal">
                            <div>添加试题</div>
                            {consolidateQuestionIds_detail &&
                            Object.keys(consolidateQuestionIds_detail).length > 0 && (
                                <div>
                                    {Object.keys(consolidateQuestionIds_detail).map((ser) => {
                                        return (
                                            <Main
                                                result={[consolidateQuestionIds_detail[ser]]}
                                                showId="createPlanPaper"
                                            />
                                        );
                                    })}
                                </div>
                            )}
                            <AddPlanModal
                                disabled={!isEditable}
                                onChange={this.handleChange}
                                Main="consolidateQuestionIds"
                            />
                            <div className="parentName">
                                <span>家长签字:</span>
                                <Input
                                    className="text"
                                    disabled={!isEditable}
                                    value={planParams['consolidateSignature'] || ''}
                                    onChange={(e) => this.handleChange('consolidateSignature', e.target.value)}
                                />
                            </div>
                        </div>
                        <div className="plan-content-title">每日一练</div>
                        {CONST.Daily_Test.map((item, index) => {
                            let showDetail = {};
                            const num = teachPracticeList.findIndex((m) => m.week && m.week === item.label || m.week === item.key);
                            if (num > -1) {
                                showDetail = teachPracticeList[num]
                            }
                            return (
                                <div className="content-modal" key={item.label}>
                                    <div>{`${item.label}闯关习题`}</div>
                                    {showDetail && showDetail['practiceQuestionIds_detail'] && (
                                        <div>
                                            {Object.keys(showDetail['practiceQuestionIds_detail']).map((td) => {
                                                return (
                                                    <Main
                                                        result={[showDetail['practiceQuestionIds_detail'][td]]}
                                                        showId="createPlanPaper"
                                                    />
                                                );
                                            })}
                                        </div>
                                    )}
                                    <AddPlanModal
                                        disabled={!isEditable}
                                        onChange={this.handleChange}
                                        Main="practiceQuestionIds"
                                        index={item.label}
                                        parentId="teachPracticeList"
                                    />
                                    <div className="parentName">
                                        <span>家长签字:</span>
                                        <Input
                                            className="text"
                                            disabled={!isEditable}
                                            value={showDetail && showDetail.signature || ''}
                                            onChange={(e) =>
                                                this.handleChange('signature', e.target.value, num < 0 ? index : num, 'teachPracticeList', item.label)
                                            }
                                        />
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                )}
                {isShowCompleted && (
                    <div className="plan-foot">
                        {planType !== 'add' && (
                            <Button
                                onClick={() => this.handlePlan('disShowAll', planParams)}
                                style={{margin: 'auto', display: 'block'}}
                            >
                                折叠
                            </Button>
                        )}
                        {planType !== 'showPlan' && (
                            <Button onClick={this.handleCreatePlan} style={{margin: 'auto', display: 'block'}}>
                                {planType === 'add' ? '创建教案' : '保存教案'}
                            </Button>
                        )}
                    </div>
                )}
                {!isShowCompleted && (
                    <div className="plan-foot">
            <span>
              <a
                  href={`http://47.112.115.39:39001/teach/download?teachId=${planParams.id || 1}`}
              >
                下载word格式
              </a>
            </span>
                        {planType === 'selfPlan' ? (
                            <span onClick={() => this.handlePlan('showAllEdit', planParams)}>查看并修改</span>
                        ) : (
                            <span onClick={() => this.handlePlan('showAll', planParams)}>完整查看</span>
                        )}
                        {planType === 'showPlan' && (
                            <span onClick={() => this.handlePlan('addSelf', planParams)}>添加到我的个人中心</span>
                        )}
                        {planType !== 'showPlan' && (
                            <span onClick={() => this.handlePlan('delete', planParams)}>删除教案</span>
                        )}
                    </div>
                )}
            </div>
        );
    }
}

export default Plan;
