import React, { Component } from 'react';
import { Layout, Button } from 'antd';
import memoryUtils from '../../utils/memoryUtils';

import { withRouter } from 'react-router-dom';
import './style.less';

const { Header } = Layout;

class TeachHeader extends Component {
  handleClick = () => {
    this.props.history.replace('/');
  };

  handleCreateAccount = () => {
    // 跳转到创建账号
    this.props.history.replace('/create-account');
  };

  handleMyClick = () => {
    this.props.history.replace('/my-main');
  };

  render() {
    const {
      location: { pathname },
    } = this.props;

    const user = memoryUtils.user;
    let showCreateAccount = false;
    if (user && user.userType === '1') {
      showCreateAccount = pathname === '/home';
    }

    return (
      <Header>
        {pathname !== '/home' && <Button onClick={this.handleClick}>返回</Button>}
        <Button className="teacher-header-btn" onClick={this.handleMyClick}>
          个人中心
        </Button>
        {showCreateAccount && (
          <Button className="teacher-header-btn" onClick={this.handleCreateAccount}>
            创建账号
          </Button>
        )}
      </Header>
    );
  }
}

export default withRouter(TeachHeader);
