import React from 'react';
import { Modal } from 'antd';
import './style.less';
import {reqGetMyQuestions, reqGetQuestionsLeftMenu, reqGetQuestions, reqGetQuestionsAnswer} from '../../api';
import TeacherTree from '../TeacherTree';
import Paper from '../Paper';

class AddPlanModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      value: [],
      paperTreeList: [],
      showPaperResult: [],
    };
    this.paperQueryParams = {
      complexity: null,
      course: null,
      grade: null,
      knowledge: null,
      label: null,
      questionsType: null,
      year: null,
    };
  }

  showModal = () => {
    const { disabled } = this.props;
    console.log(disabled);
    this.setState(
      {
        visible: true,
      },
      this.loadLeft
    );
  };

  async loadLeft() {
    const paperTreeListResult = await reqGetQuestionsLeftMenu();
    if (paperTreeListResult.data) {
      this.setState({ paperTreeList: paperTreeListResult.data });
    }
    this.load();
  }

  componentWillReceiveProps(next) {
    if (this.props.Main !== next.Main) {
      delete this[this.props.Main];
    }
  }

  handleOk = (e) => {
    const { onChange, Main, index, parentId } = this.props;
    this.setState({
      visible: false,
    });
    if (this[Main] && Object.keys(this[Main]).length > 0) {
      onChange(Main, this[Main], index, parentId);
    }
  };

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  };
  handleChange = (value, key) => {
    const [grade, course, knowledge] = value.split('|');
    const Obj = {
      grade,
      course,
      knowledge,
    };
    this.load(Obj);
  };
  handleChangePaper = async (key, value, type) => {
    const { Main } = this.props;
    if (key === 'planPaper') {
      if (type === 'add') {
        const result = await reqGetQuestionsAnswer({questionsId:value.id});
        if(result.data){
          value = {...value,...result.data}
        }
        // if(value.id){}
        // this[Main] = this[Main] ? {...this[Main],[value.id]:value} : {[value.id] : value};
       this[Main] = this[Main] ? {...this[Main],[value.id]:value}: {[value.id]:value};
      }
    }
  };
  async load(obj) {
    const objNew = obj || {};
    const payload = {
      ...this.paperQueryParams,
      ...objNew,
    };
    const showPaperResult = await reqGetMyQuestions(payload);
    //const showPaperResult = await reqGetQuestions(payload);
    if (showPaperResult.data) {
      this.setState({ showPaperResult: showPaperResult.data, showType: 'paper' });
    }
  }

  render() {
    const { Main } = this.props;
    const arr = ['seniorQuestion', 'pointsQuestion'];
    const { paperTreeList, showPaperResult } = this.state;
    return (
      <div className="my-plan">
        {Main && arr.indexOf(Main) > -1 ? (
          <div className="add-btn" onClick={this.showModal}>
            +添加试题
          </div>
        ) : (
          <div onClick={this.showModal} className="content-btn">
            +
          </div>
        )}
        <Modal
          title="添加试题"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          width={800}
        >
          <div style={{ display: 'flex', height: 600, overflow: 'auto' }}>
            <div>
              <TeacherTree
                treeList={paperTreeList}
                style={{ width: 100 }}
                keyId="planPaper"
                onChange={this.handleChange}
              />
            </div>
            <div className="showPaper-content">
              <Paper
                result={showPaperResult}
                showId="planPaper"
                onChange={this.handleChangePaper}
              />
            </div>
          </div>
        </Modal>
      </div>
    );
  }
}

export default AddPlanModal;
