import React, { Component } from 'react';
import { Button } from 'antd';
import { showPlanType } from './Variable';
import TeacherTree from '../../components/TeacherTree';
import { reqGetLeftMenu, reqSearchTeach, reqGetAllTeaches } from '../../api';
import PlanCard from '../../components/PlanCard';
import Plan from '../../components/Plan';
import memoryUtils from '../../utils/memoryUtils';

class ShowPlan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectPlan: {},
      treeList: [],
      planType: {},
      result: [],
      grade: '',
      course: '',
    };
  }

  async componentDidMount() {
    const result = await reqSearchTeach();
    const Leftresult = await reqGetLeftMenu();
    const newResult = await reqGetAllTeaches();
    if (Leftresult.data) {
      this.setState({ treeList: Leftresult.data });
    }
    if (newResult.data) {
      this.setState({ planType: newResult.data });
    }
    if (result.data) {
      this.setState({ result: result.data });
    }
  }

  handleClick = (key, d) => {
    const { selectPlan } = this.state;
    const newSelect = selectPlan[key] ? [...selectPlan[key]] : [d];
    if (selectPlan[key]) {
      const num = selectPlan[key] ? selectPlan[key].indexOf(d) : -1;
      if (num > -1) {
        newSelect.splice(num, 1);
      } else {
        newSelect.push(d);
      }
    }
    this.setState({ selectPlan: { ...selectPlan, [key]: newSelect } }, this.load);
  };
  handleChange = (value) => {
    // key,value
    if (value.length > 0) {
      const valueArr = value[0].split('|');
      this.setState({ grade: valueArr[1] || null, course: valueArr[2] || null }, this.load);
    }else{
      this.setState({grade:null,course:null}, this.load)
    }
  };

  async load() {
    const { grade, selectPlan, course } = this.state;
    const user = memoryUtils.user;
    const payload = {
      ...selectPlan,
      grade: grade !== '' ? grade : null,
      course: course !== '' ? course : null,
      id: user.id,
    };
    const Result = await reqSearchTeach(payload);
    if (Result.data) {
      this.setState({ result: Result.data });
    }
  }
  render() {
    const { selectPlan, treeList, planType, result } = this.state;
    return (
      <div className="createPlan">
        <div className="createPlan-left">
          <Button>教案选取</Button>
          <TeacherTree treeList={[...treeList]} onChange={this.handleChange} />
        </div>
        <div className="createPlan-right">
          <div className="plan-filter">
            {Object.keys(showPlanType).map((t, index) => {
              return (
                <div className="tab-main" key={index}>
                  <span key={t} className="tab-main-text">
                    {t}
                  </span>
                  <div style={{width:'calc(100% - 120px)',display:'flex'}}>
                    {((planType[showPlanType[t]] && planType[showPlanType[t]]) || []).map((d) => {
                      return (
                        <span
                          key={`${t}-${d}`}
                          className={`tab-main-span ${
                            (selectPlan[showPlanType[t]] || []).indexOf(d) > -1 ? 'selected' : ''
                          }`}
                          onClick={() => {
                            this.handleClick(showPlanType[t], d);
                          }}
                        >
                          {d}
                        </span>
                      );
                    })}
                  </div>
                </div>
              );
            })}
          </div>
          <div className="plan-main">
            {result.map((plan, index) => {
              //   return <PlanCard result={it} key={index} />;
              return <Plan key={index} planParams={plan} planType="showPlan" />;
            })}
          </div>
        </div>
      </div>
    );
  }
}

export default ShowPlan;
