import React, { Component } from 'react';
import TeacherTree from '../../components/TeacherTree';
import MyPaper from './MyPaper';
import MyPlan from './MyPlan';

import './style.less';

import {
  reqGetQuestionsLeftMenu,
  reqGetMyQuestions,
  reqGetLeftMenu,
  reqSearchMyTeach,
} from '../../api';
import memoryUtils from '../../utils/memoryUtils';

class Main extends Component {
  // 个人中心、
  constructor(props) {
    super(props);
    this.state = {
      planTreeList: [],
      paperTreeList: [],
      showPaperResult: [],
      showType: 'paper', // 默认显示查看试题
    };
    this.paperQueryParams = {
      complexity: null,
      course: null,
      grade: null,
      knowledge: null,
      label: null,
      questionsType: null,
      year: null,
    };
  }

  async componentDidMount() {
    const paperTreeListResult = await reqGetQuestionsLeftMenu();
    const planTreeListResult = await reqGetLeftMenu();
    const showPaperResult = await reqGetMyQuestions(this.paperQueryParams);
    const showPlanResult = await reqSearchMyTeach({
      grade: null,
      course: null,
    });

    if (paperTreeListResult.data) {
      this.setState({ paperTreeList: paperTreeListResult.data });
    }

    if (planTreeListResult.data) {
      this.setState({ planTreeList: planTreeListResult.data });
    }

    if (showPaperResult.data) {
      this.setState({ showPaperResult: showPaperResult.data });
    }

    if (showPlanResult.data) {
      this.setState({ showPlanResult: showPlanResult.data });
    }
  }

  handleChange = (type) => (value) => {
    const user = memoryUtils.user;
    if (type === 'paper') {
      let obj ={
        grade:  null,
        course:  null,
        knowledge:  null,
      }
      if(value.length > 0){
        const valueArr = value[0].split('|');
         obj ={
          grade: valueArr[1] || null,
          course: valueArr[2] || null,
          knowledge: valueArr[3] || null,
        }
      }
      this.paperQueryParams = {
        ...this.paperQueryParams,
       ...obj,
        id: user.id,
      };
      this.loadPaper();
    } else if (type === 'plan') {
      let paperQueryParams = {
        grade:  null,
        course:  null,
      };
      if(value.length >0){
        const valueArr = value[0].split('|');
         paperQueryParams = {
          grade: valueArr[1] || null,
          course: valueArr[2] || null,
        };
      }


      this.loadPlan(paperQueryParams);
    }
  };

  handleTypeChange = (showType) => {
    this.setState({ showType });
  };

  loadPaper = async () => {
    const showPaperResult = await reqGetMyQuestions(this.paperQueryParams);
    if (showPaperResult.data) {
      this.setState({ showPaperResult: showPaperResult.data, showType: 'paper' });
    }
  };

  loadPlan = async (params) => {
    const Result = await reqSearchMyTeach(params);
    if (Result.data) {
      this.setState({ showPlanResult: Result.data, showType: 'plan' });
    }
  };

  renderLeftMenu = () => {
    const { planTreeList, paperTreeList, showType } = this.state;

    return (
      <div className="main-left">
        <h5 onClick={() => this.handleTypeChange('plan')}>我的教案</h5>
        {showType === 'plan' && (
          <TeacherTree treeList={[...planTreeList]} onChange={this.handleChange('plan')} />
        )}
        <h5 onClick={() => this.handleTypeChange('paper')}>我的试题</h5>
        {showType === 'paper' && (
          <TeacherTree treeList={[...paperTreeList]} onChange={this.handleChange('paper')} />
        )}
      </div>
    );
  };
  render() {
    const { showPaperResult, showPlanResult, showType } = this.state;
    return (
      <div className="main">
        {this.renderLeftMenu()}
        <div className="main-right">
          {showType === 'paper' && <MyPaper papers={showPaperResult} />}
          {showType === 'plan' && <MyPlan plans={showPlanResult || []} />}
        </div>
      </div>
    );
  }
}

export default Main;
